.avatar{
    padding-top: 195px;
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    justify-content: center;
    align-items: center;
    color: black;
    z-index: 98;
    visibility: hidden;
    opacity: 0;
    transition: visibility 1s, opacity 1s linear;
    pointer-events: none;}